import "./styles.css";
import { Container } from "react-bootstrap";
import { Stack, Button } from "react-bootstrap";
import BudgetCard from "./components/BudgetCard";
import AddBudgetModal from "./components/AddBudgetModal";
import React, { useState } from "react";
import { useBudgets } from "./contexts/BudgetsContext";
import AddExpenseModal from "./components/AddExpenseModal";
import UncategorizedBudgetCard from "./components/UncategorizedBudgetCard";
import TotalBudgetCard from "./components/TotalBudgetCard";
import ViewExpenseModal from "./components/ViewExpenseModal";
export const UNCATEGORIZED_BUDGET_ID = "uncategorized";

export default function App() {
  const [showAddBudgetModel, setShowAddBudgetModel] = useState(false);
  const [showAddExpenseModel, setShowAddExpenseModel] = useState(false);
  const [viewExpenseModelBudgetId, setViewExpenseModelBudgetId] = useState();

  const [
    showAddExpenseModelBudgetId,
    setShowAddExpenseModelBudgetId
  ] = useState("");
  function openAddExpenseModel(budgetId) {
    setShowAddExpenseModel(true);
    setShowAddExpenseModelBudgetId(budgetId);
  }

  const { budgets, getBudgetExpense } = useBudgets();
  return (
    <>
      <Container className="my-4">
        <Stack direction="horizontal" gap="2" className="mb-4">
          <h1 className="me-auto">Budgets</h1>
          <Button vairant="primary" onClick={() => setShowAddBudgetModel(true)}>
            Add Budget
          </Button>
          <Button
            variant="outline-primary"
            onClick={() => openAddExpenseModel()}
          >
            {" "}
            Add Expense
          </Button>
        </Stack>
        <div
          style={{
            display: "grid",
            gridTemplateColumns: "repeat(auto-fill,minmax(300px,1fr))",
            gap: "1rem",
            alignItems: "flex-start"
          }}
        >
          {budgets.map((budget) => {
            const amount = getBudgetExpense(budget.id).reduce(
              (total, expense) => total + expense.amount,
              0
            );
            return (
              <BudgetCard
                key={budget.id}
                name={budget.name}
                amount={amount}
                max={budget.max}
                onAddExpenseClick={() => openAddExpenseModel(budget.id)}
                onViewExpenseClick={() =>
                  setViewExpenseModelBudgetId(budget.id)
                }
              >
                {" "}
              </BudgetCard>
            );
          })}
          <UncategorizedBudgetCard
            handleClose={() => {
              setShowAddExpenseModel(false);
            }}
            onViewExpenseClick={() =>
              setViewExpenseModelBudgetId(UNCATEGORIZED_BUDGET_ID)
            }
          />
          <TotalBudgetCard />
        </div>
      </Container>
      <AddBudgetModal
        show={showAddBudgetModel}
        handleClose={() => {
          setShowAddBudgetModel(false);
        }}
      />
      <ViewExpenseModal
        budgetId={viewExpenseModelBudgetId}
        handleClose={() => {
          setViewExpenseModelBudgetId();
        }}
      />
      <AddExpenseModal
        show={showAddExpenseModel}
        defaultBudgetId={showAddExpenseModelBudgetId}
        handleClose={() => {
          setShowAddExpenseModel(false);
        }}
      />
    </>
  );
}
